<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
        <FormItem label="培训主办单位：">
          <label>
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val1"
            >
              <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val1}}</p>
            </el-popover>
          </label>
        </FormItem>
        <FormItem label="参训对象：">
          <label>{{watchTableCell.val2}}</label>
        </FormItem>
        <FormItem label="培训时间：">
          <label>{{watchTableCell.val3[0]}}~{{watchTableCell.val3[1]}}</label>
        </FormItem>
        <FormItem label="培训方式：">
          <label>{{watchTableCell.val4}}</label>
        </FormItem>
        <FormItem label="培训天数：">
          <label>{{watchTableCell.val5}}</label>
        </FormItem>
        <FormItem label="培训人次：">
          <label>{{watchTableCell.val6}}</label>
        </FormItem>
        <FormItem label="培训内容：">
          <label>{{watchTableCell.val7}}</label>
        </FormItem>
        <FormItem label="培训效果：">
          <label>{{watchTableCell.val8}}</label>
        </FormItem>
        <FormItem label="是否公开">
          <label>{{watchTableCell.val9?'是':'否'}}</label>
        </FormItem>
        <FormItem label="编辑内容：">
          <label>
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val10"
            >
              <p slot="reference" class="textMaxWidth2 disBlock textOverflow">{{watchTableCell.val10}}</p>
            </el-popover>
          </label>
        </FormItem>
        <FormItem label="附件：">
          <showUsualFiles
            :file-obj="dataObj4"
            :key="fileId4"
          ></showUsualFiles>
        </FormItem>
        <FormItem label="追加附件：">
          <showUsualFiles
            :file-obj="dataObj5"
            :key="fileId5"
          ></showUsualFiles>
        </FormItem>
        <FormItem label="追加内容：">
          <label>
            <el-popover
              placement="top-start"
              title
              width="200"
              trigger="hover"
              :content="watchTableCell.val11"
            >
              <p slot="reference" class="textMaxWidth2 disBlock textOverflow">{{watchTableCell.val11}}</p>
            </el-popover>
          </label>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      watchTableCell: {
        bpmTitle: "",
        val1: "",
        val2: "",
        val3: ["", ""],
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: "",
        nextUser: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      // ++++++++++
      this.axios
        .post(`/process/infTrain/getEntityRow/${val3}`+'?infViewNum=1')
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.attachId = response.data.attachId;
            this.watchTableCell.bpmTitle = "*******";
            (this.watchTableCell.val1 = response.data.hostUnit),
              (this.watchTableCell.val2 = response.data.trainee),
              (this.watchTableCell.val3 = []);
            this.watchTableCell.val3.push(response.data.trainStartTime);
            this.watchTableCell.val3.push(response.data.trainEndTime);
            (this.watchTableCell.val4 = response.data.trainType),
              (this.watchTableCell.val5 = response.data.trainDays),
              (this.watchTableCell.val6 = response.data.trainNum),
              (this.watchTableCell.val7 = response.data.trainContent),
              (this.watchTableCell.val8 = response.data.trainResult),
              (this.watchTableCell.val9 = JSON.parse(response.data.ifShow)),
              (this.watchTableCell.val10 = response.data.content),
              (this.dataObj4 = {
                data: response.data,
                showFlag: false,
              });

            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
            };
            this.fileId5 = !this.fileId5;

          }
        })
        .catch(function(error) {});
    }
  }
};
</script>


